<template>
  <!--  -->
  <!-- v-loadingelement loading+ fullscreen -->
  <!-- Loading  -->
  <div class="rank-wrap container" v-loading="screenLoading">
    <div class="module">
      <h2 class="title"></h2>
      <song-sheet :sheet-list="featureList"></song-sheet>
    </div>
    <div class="module">
      <h2 class="title"></h2>
      <song-sheet :sheet-list="globalList"></song-sheet>
    </div>
  </div>
</template>

<script>
import songSheet from '../../components/content/song-sheet/index'
export default {
  name: 'rank',
  data() {
    return {
      // 
      ranks: [],
      // 
      featureList: [],
      // 
      globalList: [],
      // 
      screenLoading: false
    }
  },
  components: {
    songSheet
  },
  created() {
    this.getRank()
  },
  methods: {
    // 
    async getRank() {
      this.screenLoading = true
      try {
        const res = await this.$api.getRank()
        if (res.code === 200) {
          this.ranks = res.list
          this.featureList = res.list.slice(0, 4)
          this.globalList = res.list.slice(4)
        }
        // trycatch
        this.screenLoading = false
      } catch (e) {
        this.screenLoading = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.rank-wrap {
  .module {
    .title {
      width: 100%;
      height: 20px;
      padding-left: 15px;
      margin-bottom: 20px;
      border-left: 3px solid #fa2800;
    }
  }
}
</style>