/**
 * jQuery animate 
 *  https://www.jb51.net/article/86785.htm
 * @param {} destination
 * @param {} duration
 * @param {} easing
 * @param {} callback
 */
export function scrollIt(
  destination = 0,
  duration = 200,
  easing = 'linear',
  callback,
) {
  let easings = {
    // no easing, no acceleration
    linear(t) {
      return t
    },
    // accelerating from zero velocity

    easeInQuad(t) {
      return t * t
    },
    // decelerating to zero velocity
    easeOutQuad(t) {
      return t * (2 - t)
    },
    // acceleration until halfway, then deceleration
    easeInOutQuad(t) {
      return t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t
    },
    // accelerating from zero velocity
    easeInCubic(t) {
      return t * t * t
    },
    // decelerating to zero velocity
    easeOutCubic(t) {
      return --t * t * t + 1
    },
    // acceleration until halfway, then deceleration
    easeInOutCubic(t) {
      return t < 0.5 ? 4 * t * t * t : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1
    },
    // accelerating from zero velocity
    easeInQuart(t) {
      return t * t * t * t
    },
    // decelerating to zero velocity
    easeOutQuart(t) {
      return 1 - --t * t * t * t
    },
    // acceleration until halfway, then deceleration
    easeInOutQuart(t) {
      return t < 0.5 ? 8 * t * t * t * t : 1 - 8 * --t * t * t * t
    },
    // accelerating from zero velocity
    easeInQuint(t) {
      return t * t * t * t * t
    },
    // decelerating to zero velocity
    easeOutQuint(t) {
      return 1 + --t * t * t * t * t
    },
    // acceleration until halfway, then deceleration
    easeInOutQuint(t) {
      return t < 0.5 ? 16 * t * t * t * t * t : 1 + 16 * --t * t * t * t * t
    },
  }
  // window.requestAnimationFrame() ()
  // setTimeoutrequestAnimationFrame
  // requestAnimationFrame
  // requestAnimationFrame()
  // 
  ;(function() {
    let lastTime = 0
    if (!window.requestAnimationFrame) {
      window.requestAnimationFrame =
        window.webkitRequestAnimationFrame ||
        window.msRequestAnimationFrame ||
        window.mozRequestAnimationFrame ||
        window.oRequestAnimationFrame ||
        function(callback) {
          let currentTime = new Date().getTime()
          // 16.7ms1000/60,
          // currentTime - lastTimewindow.requestAnimationFrame
          // 16.7 - (currentTime - lastTime)setTimeoutmcallback16.7ms
          // callback
          let timeToCall = Math.max(0, 16.7 - (currentTime - lastTime))
          let timer = window.setTimeout(function() {
            callback(currentTime + timeTocall)
          }, timeToCall)
          lastTime = currentTime + timeToCall
          return timer
        }
    }
    if (!window.cancelAnimationFrame) {
      window.cancelAnimationFrame =
        window.webkitCancelAnimationFrame ||
        window.webkitCancelRequestAnimationFrame ||
        window.mozCancelAnimationFrame ||
        window.mozCancelRequestAnimationFrame ||
        window.msCancelAnimationFrame ||
        window.msCancelRequestAnimationFrame ||
        window.oCancelAnimationFrame ||
        window.oCancelRequestAnimationFrame ||
        function(id) {
          window.clearTimeout(id)
        }
    }
  })()

  let element = checkElement()
  let start = element.scrollTop // y
  // Date.now()new Date().getTime()
  // new Date().getTime()
  let startTime = new Date().getTime() // 

  function scroll() {
    let nowTime = new Date().getTime()
    let time = Math.min(1, (nowTime - startTime) / duration)
    let timeFunction = easings[easing](time)
    element.scrollTop = timeFunction * (destination - start) + start
    if (element.scrollTop === destination) {
      callback // 
      return
    }
    window.requestAnimationFrame(scroll)
  }
  scroll()
}

function checkElement() {
  // chrome,safaridocumentElement
  // chrome,safaridocumentElement.scrollTopundefined
  // chrome,safaridocumentElement.scrollTopdocument.body.scrollTop0
  let element = document.documentElement.scrollTop
    ? document.documentElement
    : document.body
  return element
}
