<template>
  <div class="banner" v-if="bannerLength">
    <swiper ref="mySwiper" :options="swiperOptions">
      <swiper-slide v-for="(item,index) in banner" :key="index">
        <img :src="item.imageUrl" alt="">
      </swiper-slide>
    </swiper>
    <!-- swiper-container -->
    <div class="swiper-pagination" slot="pagination"></div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
// import 'swiper/css/swiper.css'

export default {
  name: 'banner',
  data() {
    return {
      banner: [],
      // swiper v4.1.1 APIhttps://www.swiper.com.cn/api/index.html
      swiperOptions: {
        autoplay: {
          delay: 3000,
          disableOnInteraction: false // falseswiperautoplay
        },
        slidesPerView: 3, //sliderslides(carousel)
        slidesPerGroup: 3, //carousel modeslides
        spaceBetween: 30, //slidepx
        loop: true, //
        loopFillGroupWithBlank: true, //loopgroupslide
        // centeredSlides: true, //trueactive slide
        grabCursor: true, //SwipertrueSwiper
        pagination: {
          el: '.swiper-pagination', //cssHTML
          clickable: true //trueSwiper
        }
      }
    }
  },
  components: {
    Swiper,
    SwiperSlide
  },
  computed: {
    bannerLength() {
      return this.banner.length ? true : false
    }
  },
  methods: {
    async getBanner() {
      const res = await this.$api.getBanner()
      this.banner = res.banners
    }
  },
  created() {
    this.getBanner()
  },
  mounted() {}
}
</script>

<style lang="scss" scoped>
.banner {
  position: relative;
  .swiper-slide {
    img {
      border-radius: 3px;
    }
  }
}
.banner ::v-deep .swiper-pagination {
  position: absolute;
  bottom: -20px;
  width: 100%;
  .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    margin: 0 5px;
    border-radius: 50%;
    opacity: 0.8;
    background-color: #a3a3ac;
  }
  .swiper-pagination-bullet:focus {
    outline: none;
  }
  .swiper-pagination-bullet-active {
    width: 15px;
    height: 6px;
    border: 0;
    border-radius: 4px;
    background: #fa2800;
    opacity: 1;
  }
}
</style>