<template>
  <!--  -->
  <div class="singer-wrap container">
    <!--  -->
    <div class="filter">
      <ul class="tag">
        <li :class="currentArea === item.value?'active':''"
          v-for="item in areas" :key="item.value"
          @click="chooseType('area',item.value)">
          {{item.label}}
        </li>
      </ul>
      <ul class="tag">
        <li :class="currentType===item.value?'active':''" v-for="item in types"
          :key="item.value" @click="chooseType('type',item.value)">
          {{item.label}}
        </li>
      </ul>
      <ul class="tag tag-letter">
        <li :class="currentLetter===item.value?'active':''"
          v-for="item in letters" :key="item.value"
          @click="chooseType('letter',item.value)">
          {{item.label}}
        </li>
      </ul>
    </div>
    <!--  -->
    <load-more @loadmore="loadMore">
      <singer-list :singers="singers"></singer-list>
    </load-more>
    <loading-icon v-if="showLoadIcon" :loading-text="'~'"></loading-icon>
  </div>
</template>

<script>
import loadMore from '../../components/common/LoadMore'
import singerList from '../../components/content/singer-list/index'
import loadingIcon from '../../components/common/LoadingIcon'
export default {
  name: 'singer',
  data() {
    return {
      // /
      currentArea: -1,
      // 
      areas: [
        { value: -1, label: '' },
        { value: 7, label: '' },
        { value: 96, label: '' },
        { value: 8, label: '' },
        { value: 16, label: '' },
        { value: 0, label: '' }
      ],
      // 
      currentType: -1,
      // 
      types: [
        { value: -1, label: '' },
        { value: 1, label: '' },
        { value: 2, label: '' },
        { value: 3, label: '' }
      ],
      // 
      currentLetter: -1,
      // A-Z
      letters: [],
      // 
      params: {
        // 
        limit: 40,
        // 
        offset: 0,
        // 
        type: -1,
        // 
        area: -1,
        // A-Z,-1,0
        initial: -1
      },
      // 
      singers: [],
      // 
      hasMore: false,
      // 
      showLoadIcon: false
    }
  },
  components: {
    loadMore,
    singerList,
    loadingIcon
  },
  created() {
    this.setLetters()
    this.getSingers()
  },
  methods: {
    // 
    setLetters() {
      for (let i = 0; i < 26; i++) {
        this.letters.push({
          // fromCharCode  Unicode 
          // valueUIlabel
          value: String.fromCharCode(97 + i),
          label: String.fromCharCode(65 + i)
        })
      }
      this.letters.unshift({
        value: -1,
        label: ''
      })
      this.letters.push({
        value: 0,
        label: ''
      })
    },
    // 
    chooseType(type, val) {
      if (type === 'area') {
        this.currentArea = val
        this.params.area = val
      } else if (type === 'type') {
        this.currentType = val
        this.params.type = val
      } else if (type === 'letter') {
        this.currentLetter = val
        this.params.initial = val
      }
      this.params.offset = 0
      this.getSingers()
    },
    // 
    async getSingers() {
      const res = await this.$api.getSingers(this.params)
      if (res.code === 200) {
        this.singers.push(...res.artists)
        this.hasMore = res.more
      }
    },
    // 
    loadMore() {
      if (this.hasMore) {
        this.showLoadIcon = true
        this.params.offset += 40
        this.getSingers()
          .then(res => {
            this.showLoadIcon = false
          })
          .catch(e => {
            this.showLoadIcon = false
          })
      }
    }
  }
}
</script>

