<template>
  <!--  -->
  <!-- v-show,backTop -->
  <div class="back-top" @click="backTop" v-if="showBackTop"
    @mouseenter="showTip" @mouseleave="hideTip">
    <!-- ,, -->
    <i :style="{ color: bttOption.iColor }"></i>
    <span class="tips" :class="[bttOption.iPos]"
      :style="{ color: bttOption.textColor }"
      v-show="showToolTip">{{ bttOption.text }}</span>
    <!-- <img src="../../assets/images/gotop.png" alt=""> -->
  </div>
</template>

<script>
import { scrollIt } from '../../utils/scrollIt'
export default {
  data() {
    return {
      showToolTip: false,
      showBackTop: false
    }
  },
  props: {
    // 
    text: {
      type: String,
      default: ''
    },
    // 
    textColor: {
      type: String,
      default: '#f00'
    },
    // 
    iPos: {
      type: String,
      default: 'top'
    },
    // 
    iColor: {
      type: String,
      default: '#f00'
    },
    // y
    pageY: {
      type: Number,
      default: 400
    },
    // 
    transitionName: {
      type: String,
      default: 'easeInOutQuad'
    }
  },
  computed: {
    bttOption() {
      return {
        text: this.text,
        textColor: this.textColor,
        iPos: this.iPos,
        iIcon: this.iIcon,
        iColor: this.iColor,
        iFontSize: this.iFontSize
      }
    }
  },
  methods: {
    // 
    showTip() {
      return (this.showToolTip = true)
    },
    // 
    hideTip() {
      return (this.showToolTip = false)
    },
    // 
    showBackTopBtn() {
      this.showBackTop = window.pageYOffset >= this.pageY
    },
    // 
    backTop() {
      // 
      scrollIt(0, 500, this.transitionName, this.showBackTopBtn)
    }
  },
  mounted() {
    // windowscroll
    window.addEventListener('scroll', this.showBackTopBtn)
  },
  beforeDestroy() {
    // windowscroll
    window.removeEventListener('scroll', this.showBackTopBtn)
  }
}
</script>

<style>
.back-top {
  position: fixed;
  right: 120px;
  bottom: 120px;
  z-index: 999;
  cursor: pointer;
}
.back-top i {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 5px;
  font-size: 32px;
  text-align: center;
  transition: all 0.3s;
}
.back-top i::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid currentColor;
}
.back-top i:hover {
  border-radius: 50%;
  background-color: #222;
  color: #fff;
}
.back-top .tips {
  position: absolute;
  z-index: 999;
  display: inline-block;
  white-space: nowrap;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.3s;
}
.back-top .tips.left {
  top: 50%;
  left: -130%;
  transform: translateY(-50%);
}
.back-top .tips.right {
  top: 50%;
  right: -130%;
  transform: translateY(-50%);
}
.back-top .tips.top {
  top: -50%;
  left: 50%;
  transform: translateX(-50%);
}
.back-top .tips.bottom {
  bottom: -50%;
  left: 50%;
  transform: translateX(-50%);
}
</style>