      }
    }
  }
  .lyric-box {
    width: 360px;
    .title {
      margin: 0 0 20px;
      font-size: 18px;
    }
    .lyric {
      width: 100%;
      height: 430px;
      overflow: hidden;
      .lyric-wrapper {
        width: 100%;
        margin: 0 auto;
        overflow: hidden;
        .lyric-text {
          margin: 5px 0;
          line-height: 24px;
          text-align: center;
          font-size: 14px;
          font-weight: normal;
          &.active {
            color: #fa2800;
          }
        }
      }
    }
    .close-icon-wrapper {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 30px;
      line-height: 25px;
      text-align: center;
      cursor: pointer;
      transition: background-color 0.2s;
      &:hover {
        background-color: #f1f1f1;
        .close-icon {
          transform: scale(1.1) rotate(45deg);
          background-color: #f1f1f1;
        }
      }
