        text-align: center;
        border-radius: 14px;
        font-size: 13px;
        color: #333;
        cursor: pointer;
        &:hover {
          color: #fa2800;
          transition: all 0.4s;
        }
        &.active {
          background-color: #fa2800;
          color: #fff;
          font-weight: 700;
        }
      }
    }
  }
}
</style>